"use client";

import "./globals.css";
import Link from "next/link";
import { useEffect } from "react";
import { supabase } from "@/lib/supabaseClient";

export const metadata = {
  title: "Content Dashboard",
  description: "AI Content Generation System",
};

export default function RootLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  // ✅ Restore session on refresh
  useEffect(() => {
    supabase.auth.getSession();
  }, []);

  return (
    <html lang="en">
      <body style={{ margin: 0, fontFamily: "Inter, sans-serif" }}>
        <div style={{ display: "flex", minHeight: "100vh", background: "#f5f7fa" }}>
          
          {/* ✅ SIDEBAR */}
          <div
            style={{
              width: 260,
              background: "#ffffff",
              borderRight: "1px solid #e6e8eb",
              padding: 24,
              boxSizing: "border-box",
              display: "flex",
              flexDirection: "column",
            }}
          >
            <h1 style={{ fontSize: 22, fontWeight: 700, marginBottom: 24 }}>
              Dashboard
            </h1>

            <nav style={{ display: "flex", flexDirection: "column", gap: 14 }}>
              <Link href="/library" style={{ fontSize: 16, textDecoration: "none", color: "#000" }}>
                Content Library
              </Link>

              <Link href="/workspace" style={{ fontSize: 16, textDecoration: "none", color: "#000" }}>
                Content Workspace
              </Link>
            </nav>

            {/* ✅ LOGOUT BUTTON */}
            <button
              onClick={async () => {
                await supabase.auth.signOut();
                window.location.href = "/login";
              }}
              style={{
                marginTop: 30,
                padding: "10px 14px",
                background: "#eee",
                border: "1px solid #ccc",
                borderRadius: 6,
                cursor: "pointer",
              }}
            >
              Logout
            </button>
          </div>

          {/* ✅ MAIN CONTENT */}
          <main style={{ flex: 1, padding: 40 }}>{children}</main>
        </div>
      </body>
    </html>
  );
}
